.dd-hr {
    width: 95%;
    margin: 0 auto;
    border: none;
    border-top: 1px solid #DCDCDC;
    height: 0;
}

.dd-open-separator {
    background-color: white;
    border-left: 1px solid #696969;
    border-right: 1px solid #696969;
}

.dd-btn {
    border-radius: 0;
    border: none;
    background-color: white;
    padding: 8px;
}

.dd-wrapper {
    position: relative;
    width: 228px;
}

.dd-header{
    display: flex;
    justify-content: space-between;
    flex-flow: row nowrap;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 2.5rem;
    border: 1px solid #696969;
    border-radius: 5px;
    margin-bottom: 1px;
}

.dd-header-open {
    border-bottom: none;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    padding-bottom: 9px;
    margin-bottom: 0px;
}

.dd-list-open {
    border: 1px solid #696969;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border-top: none;
}

.dd-header-title {
    text-align: left;
    flex-basis: 80%;
}

.dd-header-icon {
    text-align: right;
    flex-basis: 20%;
}

.dd-list {
    position: absolute;
    z-index: 10;
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow: auto;
    height: 250px;
}

.dd-list-item {
    height: 2.5rem;
    position: relative;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    flex-direction: row;
}

.dd-list-item-label {
    flex-basis: 80%;
    text-align: left;
}

.dd-list-item-check {
    margin: 0;
    padding: 0;
    flex-basis: 20%;
    text-align: right;
}

::-webkit-scrollbar {
    background-color: white;
    width: 6px;
}

:hover::-webkit-scrollbar {
    width: 6px;
}

:hover::-webkit-scrollbar-thumb {
    background: #A9A9A9;
}

::-webkit-scrollbar-thumb {
    background: #DCDCDC;
    border-radius: 6px;
  }
