@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@600&display=swap);
.dd-hr {
    width: 95%;
    margin: 0 auto;
    border: none;
    border-top: 1px solid #DCDCDC;
    height: 0;
}

.dd-open-separator {
    background-color: white;
    border-left: 1px solid #696969;
    border-right: 1px solid #696969;
}

.dd-btn {
    border-radius: 0;
    border: none;
    background-color: white;
    padding: 8px;
}

.dd-wrapper {
    position: relative;
    width: 228px;
}

.dd-header{
    display: flex;
    justify-content: space-between;
    flex-flow: row nowrap;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 2.5rem;
    border: 1px solid #696969;
    border-radius: 5px;
    margin-bottom: 1px;
}

.dd-header-open {
    border-bottom: none;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    padding-bottom: 9px;
    margin-bottom: 0px;
}

.dd-list-open {
    border: 1px solid #696969;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border-top: none;
}

.dd-header-title {
    text-align: left;
    flex-basis: 80%;
}

.dd-header-icon {
    text-align: right;
    flex-basis: 20%;
}

.dd-list {
    position: absolute;
    z-index: 10;
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow: auto;
    height: 250px;
}

.dd-list-item {
    height: 2.5rem;
    position: relative;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    flex-direction: row;
}

.dd-list-item-label {
    flex-basis: 80%;
    text-align: left;
}

.dd-list-item-check {
    margin: 0;
    padding: 0;
    flex-basis: 20%;
    text-align: right;
}

::-webkit-scrollbar {
    background-color: white;
    width: 6px;
}

:hover::-webkit-scrollbar {
    width: 6px;
}

:hover::-webkit-scrollbar-thumb {
    background: #A9A9A9;
}

::-webkit-scrollbar-thumb {
    background: #DCDCDC;
    border-radius: 6px;
  }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
    font-family: 'Roboto', sans-serif;
	vertical-align: baseline;
    box-sizing: border-box;
}

html {
	height: 100%;
}

body {
	height: 100%;
    background-color: #27414A;
}

#app-container {
	display: flex;
	flex-direction: column;
	min-height: 100%;
}

header {
    padding: 30px;
    display: flex;
    flex-flow: column wrap;
    background-color: #418096;
}

.left-side {
    flex-basis: 80%;
    padding-left: 15px;
}

.right-side {
    display: flex;
    flex-basis: 20%;
    justify-content: flex-end;
    align-items: flex-end;
}

#website-name {
    font-family: 'Comfortaa', cursive;
    font-size: 48px;
    color: #FFFFFF;
}
p {
    font-size: 1.10rem;
}

hr {
    width: 95%;
    margin: 0 auto;
    border: none;
    border-top: 1px solid grey;
    height: 0;
}

.forecast-container {
    background-color: white;
    display: flex;
    flex-flow: column nowrap;
    width: 35vh;
    border-radius: 6px;
}

.forecast-weather-type-container {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    margin-bottom: 10px;
}

.forecast-weather-type-img {
    margin: 8px 0px;
}

.forecast-weather-type-content {
    text-align: center;
}

.forecast-temp-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    margin: 6px;
    padding: 8px;
}

.forecast-date-container {
    text-align: center;
    margin: 5px;
}

.forecast-date {
    padding: 3px;
}
.forecast-collection-container {
    display: flex;
    flex-flow: row wrap;
    margin: 0 auto;
    justify-content: space-evenly;
}

.forecast-collection-item {
    margin: 25px;
}

ul {
    list-style-type: none;
}
.forecast-body {
    padding-top: 6.5vh;
    flex: 1 1;
    width: 75%;
    margin: 0 auto;
    background-color: #8BB4B5;
}
