p {
    font-size: 1.10rem;
}

hr {
    width: 95%;
    margin: 0 auto;
    border: none;
    border-top: 1px solid grey;
    height: 0;
}

.forecast-container {
    background-color: white;
    display: flex;
    flex-flow: column nowrap;
    width: 35vh;
    border-radius: 6px;
}

.forecast-weather-type-container {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    margin-bottom: 10px;
}

.forecast-weather-type-img {
    margin: 8px 0px;
}

.forecast-weather-type-content {
    text-align: center;
}

.forecast-temp-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    margin: 6px;
    padding: 8px;
}

.forecast-date-container {
    text-align: center;
    margin: 5px;
}

.forecast-date {
    padding: 3px;
}