@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@600&display=swap');

header {
    padding: 30px;
    display: flex;
    flex-flow: column wrap;
    background-color: #418096;
}

.left-side {
    flex-basis: 80%;
    padding-left: 15px;
}

.right-side {
    display: flex;
    flex-basis: 20%;
    justify-content: flex-end;
    align-items: flex-end;
}

#website-name {
    font-family: 'Comfortaa', cursive;
    font-size: 48px;
    color: #FFFFFF;
}